	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import yiyuanxinxi from '@/views/yiyuanxinxi/list'
	import huanzhe from '@/views/huanzhe/list'
	import keshi from '@/views/keshi/list'
	import discusszuozhenyisheng from '@/views/discusszuozhenyisheng/list'
	import storeup from '@/views/storeup/list'
	import users from '@/views/users/list'
	import jianchadan from '@/views/jianchadan/list'
	import yuyueguahao from '@/views/yuyueguahao/list'
	import jianchabaogao from '@/views/jianchabaogao/list'
	import paibanxinxi from '@/views/paibanxinxi/list'
	import binglixinxi from '@/views/binglixinxi/list'
	import yishengjiuzhen from '@/views/yishengjiuzhen/list'
	import yisheng from '@/views/yisheng/list'
	import zuozhenyisheng from '@/views/zuozhenyisheng/list'
	import config from '@/views/config/list'
	import usersCenter from '@/views/users/center'
	import yishengRegister from '@/views/yisheng/register'
	import yishengCenter from '@/views/yisheng/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/usersCenter',
			name: '',
			component: usersCenter
		}
		,{
			path: '/yishengCenter',
			name: '',
			component: yishengCenter
		}
		,{
			path: '/news',
			name: '',
			component: news
		}
		,{
			path: '/yiyuanxinxi',
			name: '',
			component: yiyuanxinxi
		}
		,{
			path: '/huanzhe',
			name: '',
			component: huanzhe
		}
		,{
			path: '/keshi',
			name: '',
			component: keshi
		}
		,{
			path: '/discusszuozhenyisheng',
			name: '-',
			component: discusszuozhenyisheng
		}
		,{
			path: '/storeup',
			name: '',
			component: storeup
		}
		,{
			path: '/users',
			name: '',
			component: users
		}
		,{
			path: '/jianchadan',
			name: '',
			component: jianchadan
		}
		,{
			path: '/yuyueguahao',
			name: '',
			component: yuyueguahao
		}
		,{
			path: '/jianchabaogao',
			name: '',
			component: jianchabaogao
		}
		,{
			path: '/paibanxinxi',
			name: '',
			component: paibanxinxi
		}
		,{
			path: '/binglixinxi',
			name: '',
			component: binglixinxi
		}
		,{
			path: '/yishengjiuzhen',
			name: '',
			component: yishengjiuzhen
		}
		,{
			path: '/yisheng',
			name: '',
			component: yisheng
		}
		,{
			path: '/zuozhenyisheng',
			name: '',
			component: zuozhenyisheng
		}
		,{
			path: '/config',
			name: '',
			component: config
		}
		]
	},
	{
		path: '/yishengRegister',
		name: '',
		component: yishengRegister
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
