<template>
	<div>
		<el-dialog modal-class="edit_form_modal" class="edit_form" v-model="formVisible" :title="formTitle" width="80%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" :rules="rules">
				<el-row >
					<el-col :span="12">
						<el-form-item label="" prop="yuyuebianhao">
							<el-input class="list_inp" v-model="form.yuyuebianhao" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.yuyuebianhao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="yishengzhanghao">
							<el-input class="list_inp" v-model="form.yishengzhanghao" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.yishengzhanghao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="yishengxingming">
							<el-input class="list_inp" v-model="form.yishengxingming" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.yishengxingming?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="huanzhezhanghao">
							<el-input class="list_inp" v-model="form.huanzhezhanghao" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.huanzhezhanghao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="huanzhexingming">
							<el-input class="list_inp" v-model="form.huanzhexingming" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.huanzhexingming?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="feiyongmingxi">
							<el-input class="list_inp" v-model.number="form.feiyongmingxi" placeholder=""
                                type="number"
								:readonly="!isAdd||disabledForm.feiyongmingxi?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="jiuzhenshijian">
							<el-date-picker
								class="list_date"
								v-model="form.jiuzhenshijian"
								format="YYYY-MM-DD HH:mm:ss"
								value-format="YYYY-MM-DD HH:mm:ss"
								type="datetime"
								:readonly="!isAdd||disabledForm.jiuzhenshijian?true:false"
								placeholder="" />
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="zhenduanwenti">
							<el-input v-model="form.zhenduanwenti" placeholder="" type="textarea"
							:readonly="!isAdd||disabledForm.zhenduanwenti?true:false"
							/>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="zhiliaofangan">
							<el-input v-model="form.zhiliaofangan" placeholder="" type="textarea"
							:readonly="!isAdd||disabledForm.zhiliaofangan?true:false"
							/>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="yaopinxinxi">
							<el-input v-model="form.yaopinxinxi" placeholder="" type="textarea"
							:readonly="!isAdd||disabledForm.yaopinxinxi?true:false"
							/>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="yizhu">
							<el-input v-model="form.yizhu" placeholder="" type="textarea"
							:readonly="!isAdd||disabledForm.yizhu?true:false"
							/>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="jiwangbingshi">
							<editor :value="form.jiwangbingshi" placeholder="" :readonly="!isAdd||disabledForm.jiwangbingshi?true:false"
								class="list_editor" @change="(e)=>editorChange(e,'jiwangbingshi')"></editor>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="jiuzhenxiangqing">
							<editor :value="form.jiuzhenxiangqing" placeholder="" :readonly="!isAdd||disabledForm.jiuzhenxiangqing?true:false"
								class="list_editor" @change="(e)=>editorChange(e,'jiuzhenxiangqing')"></editor>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="cancel_btn" @click="closeClick"></el-button>
					<el-button class="confirm_btn" type="primary" @click="save"
						>
						
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
    import {
        useStore
    } from 'vuex';
    const store = useStore()
    const user = computed(()=>store.getters['user/session'])
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//
	const tableName = 'yishengjiuzhen'
	const formName = ''
	//
	//form
	const form = ref({})
	const disabledForm = ref({
        yuyuebianhao : false,
        yishengzhanghao : false,
        yishengxingming : false,
        huanzhezhanghao : false,
        huanzhexingming : false,
        jiwangbingshi : false,
        zhenduanwenti : false,
        zhiliaofangan : false,
        jiuzhenxiangqing : false,
        yaopinxinxi : false,
        yizhu : false,
        feiyongmingxi : false,
        jiuzhenshijian : false,
        ispay : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')
    
	const rules = ref({
		yuyuebianhao: [
		],
		yishengzhanghao: [
		],
		yishengxingming: [
		],
		huanzhezhanghao: [
		],
		huanzhexingming: [
		],
		jiwangbingshi: [
		],
		zhenduanwenti: [
		],
		zhiliaofangan: [
		],
		jiuzhenxiangqing: [
		],
		yaopinxinxi: [
		],
		yizhu: [
		],
		feiyongmingxi: [
			{ validator: context.$toolUtil.validator.number, trigger: 'blur' },
		],
		jiuzhenshijian: [
		],
		ispay: [
		],
	})
	//
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')

	//
	const getUUID =()=> {
      return new Date().getTime();
    }
	//
	const resetForm = () => {
		form.value = {
			yuyuebianhao: '',
			yishengzhanghao: '',
			yishengxingming: '',
			huanzhezhanghao: '',
			huanzhexingming: '',
			jiwangbingshi: '',
			zhenduanwenti: '',
			zhiliaofangan: '',
			jiuzhenxiangqing: '',
			yaopinxinxi: '',
			yizhu: '',
			feiyongmingxi: '',
			jiuzhenshijian: '',
			ispay: '',
		}
	}
	//info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			res.data.data.jiwangbingshi = res.data.data.jiwangbingshi?(res.data.data.jiwangbingshi.replace(reg,'../../../project/file')):'';
			res.data.data.jiuzhenxiangqing = res.data.data.jiuzhenxiangqing?(res.data.data.jiuzhenxiangqing.replace(reg,'../../../project/file')):'';
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
			form.value.jiuzhenshijian = context?.$toolUtil.getCurDateTime()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '' + formName
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='yuyuebianhao'){
					form.value.yuyuebianhao = row[x];
					disabledForm.value.yuyuebianhao = true;
					continue;
				}
				if(x=='yishengzhanghao'){
					form.value.yishengzhanghao = row[x];
					disabledForm.value.yishengzhanghao = true;
					continue;
				}
				if(x=='yishengxingming'){
					form.value.yishengxingming = row[x];
					disabledForm.value.yishengxingming = true;
					continue;
				}
				if(x=='huanzhezhanghao'){
					form.value.huanzhezhanghao = row[x];
					disabledForm.value.huanzhezhanghao = true;
					continue;
				}
				if(x=='huanzhexingming'){
					form.value.huanzhexingming = row[x];
					disabledForm.value.huanzhexingming = true;
					continue;
				}
				if(x=='jiwangbingshi'){
					form.value.jiwangbingshi = row[x];
					disabledForm.value.jiwangbingshi = true;
					continue;
				}
				if(x=='zhenduanwenti'){
					form.value.zhenduanwenti = row[x];
					disabledForm.value.zhenduanwenti = true;
					continue;
				}
				if(x=='zhiliaofangan'){
					form.value.zhiliaofangan = row[x];
					disabledForm.value.zhiliaofangan = true;
					continue;
				}
				if(x=='jiuzhenxiangqing'){
					form.value.jiuzhenxiangqing = row[x];
					disabledForm.value.jiuzhenxiangqing = true;
					continue;
				}
				if(x=='yaopinxinxi'){
					form.value.yaopinxinxi = row[x];
					disabledForm.value.yaopinxinxi = true;
					continue;
				}
				if(x=='yizhu'){
					form.value.yizhu = row[x];
					disabledForm.value.yizhu = true;
					continue;
				}
				if(x=='feiyongmingxi'){
					form.value.feiyongmingxi = row[x];
					disabledForm.value.feiyongmingxi = true;
					continue;
				}
				if(x=='jiuzhenshijian'){
					form.value.jiuzhenshijian = row[x];
					disabledForm.value.jiuzhenshijian = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			form.value.ispay=''
			formVisible.value = true
		}

		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('huanzhezhanghao')&& context?.$toolUtil.storageGet("role")!=""){
				form.value.huanzhezhanghao = json.huanzhezhanghao
				disabledForm.value.huanzhezhanghao = true;
			}
			if(json.hasOwnProperty('huanzhexingming')&& context?.$toolUtil.storageGet("role")!=""){
				form.value.huanzhexingming = json.huanzhexingming
				disabledForm.value.huanzhexingming = true;
			}
		})
	}
	//
	//
	defineExpose({
		init
	})
	//
	const closeClick = () => {
		formVisible.value = false
	}
	//
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//
	const save= async ()=>{
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//
					changeCrossData(objcross)
				}else{
					crossUserId = user.value.id
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(async res=>{
								emit('formModelChange')
								context?.$toolUtil.message(``,'success')
                                formVisible.value = false
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(async (res)=>{
						emit('formModelChange')
						context?.$toolUtil.message(``,'success')
                        formVisible.value = false
					})
				}
			}else{
                context.$message.error('')
            }
		})
	}
	//
	const changeCrossData = async (row)=>{
        if(type.value == 'cross'){
            await context?.$http({
                url: `${crossTable.value}/update`,
                method: 'post',
                data: row
            }).then(res=>{})
        }
	}
</script>
<style lang="scss" scoped>
	// 
	.formModel_form{
		// form item
		:deep(.el-form-item) {
			//label
			.el-form-item__label {
			}
			// 
			.el-form-item__content {
				// 
				.list_inp {
				}
				//
				.list_date {
				}
				// 
				.list_editor {
				}
				// 
				.el-textarea__inner {
				}
			}
		}
	}
	// 
	.formModel_btn_box {
		.cancel_btn {
		}
		.cancel_btn:hover {
		}
		
		.confirm_btn {
		}
		.confirm_btn:hover {
		}
	}
</style>
