<template>
	<div>
		<el-dialog modal-class="edit_form_modal" class="edit_form" v-model="formVisible" :title="formTitle" width="80%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" :rules="rules">
				<el-row >
					<el-col :span="12">
						<el-form-item label="" prop="yishengzhanghao">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.yishengzhanghao?true:false"
								v-model="form.yishengzhanghao" 
								placeholder=""
								@change="yishengzhanghaoChange">
								<el-option v-for="(item,index) in yishengzhanghaoLists" :label="item" :value="item">
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="yishengxingming">
							<el-input class="list_inp" v-model="form.yishengxingming" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.yishengxingming?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="keshi">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.keshi?true:false"
								v-model="form.keshi" 
								placeholder=""
								>
								<el-option v-for="(item,index) in keshiLists" :label="item"
									:value="item"
									>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item prop="touxiang"
									  label=""
						>
							<uploads
								:disabled="!isAdd||disabledForm.touxiang?true:false"
								action="file/upload"
								tip=""
								style="width: 100%;text-align: left;"
								:fileUrls="form.touxiang?form.touxiang:''" 
								@change="touxiangUploadSuccess">
							</uploads>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="jiuzhenshijian">
							<el-input class="list_inp" v-model="form.jiuzhenshijian" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.jiuzhenshijian?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="guahaofei">
							<el-input class="list_inp" v-model.number="form.guahaofei" placeholder=""
                                type="number"
								:readonly="!isAdd||disabledForm.guahaofei?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="renshu">
							<el-input class="list_inp" v-model.number="form.renshu" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.renshu?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="lianxidianhua">
							<el-input class="list_inp" v-model="form.lianxidianhua" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.lianxidianhua?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="jianjie">
							<el-input v-model="form.jianjie" placeholder="" type="textarea"
							:readonly="!isAdd||disabledForm.jianjie?true:false"
							/>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="cancel_btn" @click="closeClick"></el-button>
					<el-button class="confirm_btn" type="primary" @click="save"
						>
						
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
    import {
        useStore
    } from 'vuex';
    const store = useStore()
    const user = computed(()=>store.getters['user/session'])
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//
	const tableName = 'zuozhenyisheng'
	const formName = ''
	//
	//form
	const form = ref({})
	const disabledForm = ref({
        yishengzhanghao : false,
        yishengxingming : false,
        keshi : false,
        touxiang : false,
        jiuzhenshijian : false,
        guahaofei : false,
        renshu : false,
        jianjie : false,
        lianxidianhua : false,
        storeupNumber : false,
        discussNumber : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')
    
	const rules = ref({
		yishengzhanghao: [
		],
		yishengxingming: [
		],
		keshi: [
		],
		touxiang: [
		],
		jiuzhenshijian: [
		],
		guahaofei: [
			{ validator: context.$toolUtil.validator.number, trigger: 'blur' },
		],
		renshu: [
			{ validator: context.$toolUtil.validator.intNumber, trigger: 'blur' },
		],
		jianjie: [
		],
		lianxidianhua: [
			{ validator: context.$toolUtil.validator.mobile, trigger: 'blur' },
		],
		storeupNumber: [
			{ validator: context.$toolUtil.validator.intNumber, trigger: 'blur' },
		],
		discussNumber: [
			{ validator: context.$toolUtil.validator.intNumber, trigger: 'blur' },
		],
	})
	//
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	//
	const yishengzhanghaoLists = ref([])
	//
	const keshiLists = ref([])
	//
	const touxiangUploadSuccess=(e)=>{
		form.value.touxiang = e
	}

	//
	const getUUID =()=> {
      return new Date().getTime();
    }
	//
	const resetForm = () => {
		form.value = {
			yishengzhanghao: '',
			yishengxingming: '',
			keshi: '',
			touxiang: '',
			jiuzhenshijian: '',
			guahaofei: '',
			renshu: '',
			jianjie: '',
			lianxidianhua: '',
		}
	}
	//info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '' + formName
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='yishengzhanghao'){
					form.value.yishengzhanghao = row[x];
					disabledForm.value.yishengzhanghao = true;
					continue;
				}
				if(x=='yishengxingming'){
					form.value.yishengxingming = row[x];
					disabledForm.value.yishengxingming = true;
					continue;
				}
				if(x=='keshi'){
					form.value.keshi = row[x];
					disabledForm.value.keshi = true;
					continue;
				}
				if(x=='touxiang'){
					form.value.touxiang = row[x];
					disabledForm.value.touxiang = true;
					continue;
				}
				if(x=='jiuzhenshijian'){
					form.value.jiuzhenshijian = row[x];
					disabledForm.value.jiuzhenshijian = true;
					continue;
				}
				if(x=='guahaofei'){
					form.value.guahaofei = row[x];
					disabledForm.value.guahaofei = true;
					continue;
				}
				if(x=='renshu'){
					form.value.renshu = row[x];
					disabledForm.value.renshu = true;
					continue;
				}
				if(x=='jianjie'){
					form.value.jianjie = row[x];
					disabledForm.value.jianjie = true;
					continue;
				}
				if(x=='lianxidianhua'){
					form.value.lianxidianhua = row[x];
					disabledForm.value.lianxidianhua = true;
					continue;
				}
				if(x=='storeupNumber'){
					form.value.storeupNumber = row[x];
					disabledForm.value.storeupNumber = true;
					continue;
				}
				if(x=='discussNumber'){
					form.value.discussNumber = row[x];
					disabledForm.value.discussNumber = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			formVisible.value = true
		}

		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('yishengzhanghao')&& context?.$toolUtil.storageGet("role")!=""){
				form.value.yishengzhanghao = json.yishengzhanghao
				disabledForm.value.yishengzhanghao = true;
			}
			if(json.hasOwnProperty('yishengxingming')&& context?.$toolUtil.storageGet("role")!=""){
				form.value.yishengxingming = json.yishengxingming
				disabledForm.value.yishengxingming = true;
			}
			if(json.hasOwnProperty('touxiang')&& context?.$toolUtil.storageGet("role")!=""){
				form.value.touxiang = json.touxiang
				disabledForm.value.touxiang = true;
			}
			if(json.hasOwnProperty('lianxidianhua')&& context?.$toolUtil.storageGet("role")!=""){
				form.value.lianxidianhua = json.lianxidianhua
				disabledForm.value.lianxidianhua = true;
			}
		})
		context?.$http({
			url: `option/yisheng/yishengzhanghao`,
			method: 'get'
		}).then(res=>{
			yishengzhanghaoLists.value = res.data.data
		})
		//
		disabledForm.value.yishengxingming = true;
		context?.$http({
			url: `option/keshi/keshi`,
			method: 'get'
		}).then(res=>{
			keshiLists.value = res.data.data
		})
	}
	//
	//
	defineExpose({
		init
	})
	//
	const closeClick = () => {
		formVisible.value = false
	}
	const yishengzhanghaoChange=()=>{
		context?.$http({
			url: `follow/yisheng/yishengzhanghao?columnValue=` + form.value.yishengzhanghao,
			method: 'get'
		}).then(res=>{
			if(res.data.data.yishengxingming){
				form.value.yishengxingming = res.data.data.yishengxingming
			}
		})
	}
	//
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//
	const save= async ()=>{
		if(form.value.touxiang!=null) {
			form.value.touxiang = form.value.touxiang.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//
					changeCrossData(objcross)
				}else{
					crossUserId = user.value.id
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(async res=>{
								emit('formModelChange')
								context?.$toolUtil.message(``,'success')
                                formVisible.value = false
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(async (res)=>{
						emit('formModelChange')
						context?.$toolUtil.message(``,'success')
                        formVisible.value = false
					})
				}
			}else{
                context.$message.error('')
            }
		})
	}
	//
	const changeCrossData = async (row)=>{
        if(type.value == 'cross'){
            await context?.$http({
                url: `${crossTable.value}/update`,
                method: 'post',
                data: row
            }).then(res=>{})
        }
	}
</script>
<style lang="scss" scoped>
	// 
	.formModel_form{
		// form item
		:deep(.el-form-item) {
			//label
			.el-form-item__label {
			}
			// 
			.el-form-item__content {
				// 
				.list_inp {
				}
				// 
				.list_sel {
					//
					.select-trigger{
						height: 100%;
						.el-input{
							height: 100%;
						}
					}
				}
				// 
				.el-textarea__inner {
				}
				//
				.el-upload-list  {
					//
					.el-upload__tip {
					}
					//
					.el-upload--picture-card {
						//
						.el-icon{
						}
					}
					.el-upload-list__item {
					}
				}
			}
		}
	}
	// 
	.formModel_btn_box {
		.cancel_btn {
		}
		.cancel_btn:hover {
		}
		
		.confirm_btn {
		}
		.confirm_btn:hover {
		}
	}
</style>
